import { Typography } from "@mui/material";
import React, { useState } from 'react';
import {Document, Page} from 'react-pdf/dist/esm/entry.webpack';
import pdfFile from './Machine_Learning_in_Sports_Betting__Milestone_1_.pdf'

//document help https://stackoverflow.com/questions/35517245/error-missing-class-properties-transform
//and https://stackoverflow.com/questions/47225553/load-more-than-1-pdf-pages-in-react-pdf
const Project = () => {
    const [numPages, setNumPages] = useState(null);
  const [pageNumber, setPageNumber] = useState(1);

  function loadSuccess({ numPages }) {
    setNumPages(numPages);
  }

  return (
    <div>
      <h1>Project Deliverables</h1>
        <Typography>OUR GITHUB LINK: </Typography>
        <object data={pdfFile} type="application/pdf" width="80%" height="700px">
            <p>Alternative text - include a link <a href={pdfFile}>to the PDF!</a></p>
        </object>
    
      
    </div>
  );
  

};
  
export default Project;